<template>
    <swiper :options="innerSwiperOption" class="inner-swiper-box" ref="myInnerSwiper">
    <!--<swiper  class="inner-swiper-box" ref="myInnerSwiper">-->
        <slot name="SlideTypeMainInnerContents" class="swiper-item"></slot>
        <div class="swiper-pagination main_inner" slot="pagination"></div>
    </swiper>
</template>

<script>
  import "swiper/dist/css/swiper.css"
  import {swiper, swiperSlide} from "vue-awesome-swiper"

  export default {
    name: "SlideTypeMainInner",
    computed: {
      swiper() {
        return this.$refs.myInnerSwiper.swiper
      }
    },
    components: {
      swiper,
      swiperSlide
    },
    data() {
      return {
        innerSwiperOption: {
          autoplay : false,
          pagination: {
            el: '.swiper-pagination.main_inner'
          },
          simulateTouch: false,
          clickable: false,
          loop: false,
          spaceBetween: 10,
          slidesPerView: 1.03,
          allowTouchMove: false,
          disableOnInteraction: false
        },
      }
    },
  }
</script>

<style scoped>
    .swiper-pagination.main_inner { bottom: 20px; text-align: center }
</style>
