<script>
  import "swiper/dist/css/swiper.css"
  import {swiper, swiperSlide} from "vue-awesome-swiper"
  import AnimatedNumber from "animated-number-vue"
  import SlideTypeMainInner from "@/components/Main/SlideTypeMainInner"

  export default {
    name: "SlideTypeMain",
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      },
    },
    components: {
      swiper,
      swiperSlide,
      SlideTypeMainInner,
      AnimatedNumber,
    },
    data() {
      return {
        swiperOption: {
          pagination: {
            el: '.swiper-pagination.main'
          },
        },
        bgOption: [
          'bg_main_red', 'bg_main_blue', 'bg_main_yellow', 'bg_main_green'
        ],
        resetNumber : 0,
        num1: {
          value: 0,
          default: 5127429582
        },
        num2: {
          value: 0,
          default: 7429582
        },
        duration: 1000,
      }
    },
    methods: {
      formatToPrice(value) {
        return `${Number(value).toFixed(0)}`;
      },
      changeSwiperIndex() {
        if ( this.activeIndex == 0 ) {
          this.$refs.mySwiper.$children[0].$children[0].$refs.myInnerSwiper.swiper.autoplay.start();
          this.$refs.mySwiper.$children[1].$children[0].$refs.myInnerSwiper.swiper.autoplay.stop();
          this.$refs.mySwiper.$children[1].$children[0].$refs.myInnerSwiper.swiper.slideTo(0);
        } else if ( this.activeIndex == 1 ) {
          this.$refs.mySwiper.$children[1].$children[0].$refs.myInnerSwiper.swiper.autoplay.start();
          this.$refs.mySwiper.$children[0].$children[0].$refs.myInnerSwiper.swiper.autoplay.stop();
          this.$refs.mySwiper.$children[0].$children[0].$refs.myInnerSwiper.swiper.slideTo(0);
        } else {
          this.$refs.mySwiper.$children[1].$children[0].$refs.myInnerSwiper.swiper.autoplay.stop();
          this.$refs.mySwiper.$children[1].$children[0].$refs.myInnerSwiper.swiper.slideTo(0);
          this.$refs.mySwiper.$children[0].$children[0].$refs.myInnerSwiper.swiper.autoplay.stop();
          this.$refs.mySwiper.$children[0].$children[0].$refs.myInnerSwiper.swiper.slideTo(0);
        }

      }
    },
    mounted() {
      this.$refs.mySwiper.$children[0].$children[0].$refs.myInnerSwiper.swiper.autoplay.start();

      let vm = this;
      this.swiper.on('slideChange', function(){
        vm.activeIndex = this.activeIndex;
        if( vm.activeIndex === 3) {
          vm.num1.value = vm.num1.default;
          vm.num2.value = vm.num2.default;
        }else {
          vm.num1.value = vm.resetNumber;
          vm.num2.value = vm.resetNumber;
        }
      });
    }
  }
</script>

